@import url('https://fonts.googleapis.com/css2?family=Assistant:wght@400&display=swap');

:root {
  --global-body-background: rgb(28, 1, 59); /* site background */
  --bgcolor-b2: rgb(22, 22, 22); /* repo card background */
  --bgcolor-b3: rgba(59, 59, 59, 0.37); /* commentary bg */
  /*---*/
  --global-body-forecolor: rgb(194, 194, 194); /* Title */
  --forecolor-f1: rgb(194, 194, 194); /* Title */
  --forecolor-f2: rgb(173, 173, 173); /* Description text */
  --forecolor-f3: rgb(184, 184, 184); /*repository Id*/
  --forecolor-f1a: rgb(169, 90, 177); /* Title > Link */
  --forecolor-f4: rgb(173, 173, 173); /* My Name Text */
}

body {
  margin: 0;
  font-family: 'Assistant', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--global-body-background);
  color: var(--global-body-forecolor);
}
::-webkit-scrollbar {
  background: var(--bgcolor-b1);
  width: 10pt;
}
::-webkit-scrollbar-thumb {
  background: var(--forecolor-f1);
}
